import React, { createContext, useEffect, useState, useContext, useRef } from 'react';

import validate from '../utils/validate';
import initializeData from '../utils/InitializeData';
import initStepSchedules from '../utils/initStepSchedules';
import Sounds from '../sound/Sounds';

export const StepContext = createContext();

const StepsParent = ({
    children,
    targetElementRef
}) => {

    const timerRef = useRef(null);
    const intervalRef = useRef(null); 

    const timerState = initializeData('timerState', {});
    const [steps, setSteps] = useState(initializeData('steps', []));
    const [currentStepIndex, setCurrentStepIndex] = useState(0);
    const [schedules, setSchedules] = useState(initializeData('schedules', null));
    const [schedule, setSchedule] = useState(0);
    const [stepCollapse, setStepCollapse] = useState(true);
    const [timeLeft, setTimeLeft] = useState(timerState.timeLeft || 0);
    const [currentTimer, setCurrentTimer] = useState();
    const [isRunning, setIsRunning] = useState(timerState.isRunning || false);
    const [scheduleNames, setScheduleNames] = useState(); 
    const [newStep, setNewStep] = useState('');
    const [newHours, setNewHours] = useState('');
    const [editIndex, setEditIndex] = useState();
    const [newMinutes, setNewMinutes] = useState('');
    const [newSeconds, setNewSeconds] = useState('');
    const [dialog, setDialog] = useState('');
    const [newTimer, setNewTimer] = useState('');

    const getHours = (totalSeconds) => Math.floor(totalSeconds / 3600);
    const getMinutes = (totalSeconds) => Math.floor((totalSeconds % 3600) / 60);
    const getSeconds = (totalSeconds) => totalSeconds % 60;

    const formatTime = (seconds) => {
        const hrs = Math.floor(seconds / 3600);
        const mins = Math.floor((seconds % 3600) / 60);
        const secs = seconds % 60;
        return `${hrs.toString().padStart(2, '0')}:${mins
            .toString()
            .padStart(2, '0')}:${secs.toString().padStart(2, '0')}`;
    };

    const formatTimestamp = (timestamp) => {
        if (!timestamp) return 'completed';

        const date = new Date(timestamp);

        let hours = date.getHours();
        const minutes = date.getMinutes().toString().padStart(2, '0');
        const seconds = date.getSeconds().toString().padStart(2, '0');
        const period = hours >= 12 ? 'pm' : 'am';
        hours = hours % 12 || 12;

        return `${hours}:${minutes}:${seconds} ${period}`;
    };

    const determineStepAndTimer = () => {
        if (!steps || steps.length === 0) {
            return {
                currentStep: 0,
                currentTimer: 0,
                currentStepIndex: 0,
            };
        } else {
            const now = Date.now();
            const { lastTimestamp, currentTimer, currentStepIndex, isRunning } = timerState;
            const timeElapsed = isRunning ? Math.floor((now - lastTimestamp) / 1000) : 0;
            let updatedTimer = currentTimer - timeElapsed;
            let updatedStepIndex = currentStepIndex;
            while (updatedTimer <= 0 && updatedStepIndex < steps.length) {
                updatedTimer += steps[updatedStepIndex].timer;
                updatedStepIndex++;
            }
            if (updatedStepIndex >= steps.length) {
                updatedStepIndex = 0;
                updatedTimer = steps[updatedStepIndex].timer;
            }
            return {
                currentStep: steps[updatedStepIndex],
                currentTimer: updatedTimer,
                currentStepIndex: updatedStepIndex,
            };
        }        
    };
    const moveToNextStep = () => {
        const nextIndex = currentStepIndex + 1;
        if (nextIndex < steps.length) {
            setCurrentStepIndex(nextIndex);
            setTimeLeft(steps[nextIndex].timer);
            startTimer();
        } else {
            setIsRunning(false);
        }
    };

    const pauseTimer = () => {
        clearInterval(intervalRef.current);
        intervalRef.current = null;
        setIsRunning(false);
    };
    
    const startTimer = () => {
        if (intervalRef.current) return;
        setIsRunning(true);
        intervalRef.current = setInterval(() => {
            setTimeLeft((prev) => {
                if (prev <= 1) {
                    clearInterval(intervalRef.current);
                    intervalRef.current = null;
                    moveToNextStep();
                    return 0;
                }
                return prev - 1;
            });
        }, 1000);
    };
    
    const updatedSteps = (stepIndex) => {
        
        const now = Date.now();
        let cumulativeTime = 0;
        let newSteps = [];
      
        if (schedules !== undefined) {
          console.log(`StepTimer => updatedSteps => schedules: ${JSON.stringify(schedules, null, 2)}`);
          if ((!steps || steps.length === 0) && schedules.length > 0 && schedules[0].steps) {
            newSteps = [...schedules[0].steps];
          } else {
            newSteps = [...steps];
          }
        }
      
        newSteps = newSteps.map((step, index) => {
        
            if (index < stepIndex) {
                
                const newStep = {
                    ...step,
                    stepIndex: index,
                    startTime: null,
                    endTime: null,
                };
            
                console.log(`StepContext => updateSteps => step: ${step.step}) startTime: null endTime: null`);
                console.log(`StepContext => updateSteps => newStep: ${JSON.stringify(newStep, null, 2)}`);
            
                return newStep;
            } else {        
                const startTime = now + cumulativeTime;
                cumulativeTime += step.timer * 1000;
                const endTime = now + cumulativeTime;

                const newStep = {
                    ...step,
                    stepIndex: index,
                    startTime: formatTimestamp(startTime),
                    endTime: formatTimestamp(endTime),
                };

                console.log(`StepContext => updateSteps => step: ${step.step}) startTime: ${formatTimestamp(startTime)} endTime: ${formatTimestamp(endTime)}`);
                console.log(`StepContext => updateSteps => newStep: ${JSON.stringify(newStep, null, 2)}`);

                return newStep;
            }

        });
      
        console.log(`StepContext => updatedStep => newSteps: ${JSON.stringify(newSteps, null, 2)}`);
        return newSteps;
      };
    const updateSteps = (stepIndex) => {
        if (!isRunning) return steps;        
        console.log(`StepTimer init => currentStepIndex: ${stepIndex} updatedSteps: ${JSON.stringify(updatedSteps(stepIndex), null, 2)}`);
        localStorage.setItem('steps', JSON.stringify(updatedSteps(stepIndex)));
        setSteps(updatedSteps(stepIndex));
    };
    const handleNextStep = () => {
        const nextStepIndex = currentStepIndex + 1;
        if (nextStepIndex < steps.length) {
            setCurrentStepIndex(nextStepIndex);
            setCurrentTimer(steps[nextStepIndex].timer);
        } else {
            setIsRunning(false);
            setCurrentStepIndex(-1);
        }
    };
    const handleRestartStep = () => {
        if (steps.length > 0) {
            setCurrentTimer(steps[currentStepIndex].timer);
            setIsRunning(true);
        }
    };

    const toggleTimer = () => {
        setIsRunning((prev) => !prev);
    };

    const totalSeconds =
        parseInt(newHours || 0) * 3600 +
        parseInt(newMinutes || 0) * 60 +
        parseInt(newSeconds || 0);

    const selectSchedule = (groupTitle, label, selected) => {
        if (selected === 'Add Schedule') {
            addSchedule();
            return;
        }
        if (scheduleNames !== undefined) {
            console.log(`StepTimer => selectSchedule => schedule: ${selected}`);
            console.log(`scheduleNames: ${JSON.stringify(scheduleNames, null, 2)}`);
            const scheduleIndex = scheduleNames.findIndex(c => c === selected);
            console.log(`StepContext => scheduleIndex: ${scheduleIndex}`);
            localStorage.setItem('schedule', scheduleIndex);
            setSchedule(scheduleIndex);
            setCurrentStepIndex(0);
        } else {
            const newSchedules = [...schedules];
            const newScheduleNames = newSchedules.map((schedule) => schedule.title);
            console.log(`StepTimer => selectSchedule => schedule: ${selected}`);
            console.log(`scheduleNames: ${JSON.stringify(newScheduleNames, null, 2)}`);
            const scheduleIndex = newScheduleNames.findIndex(c => c === selected);
            console.log(`StepContext => scheduleIndex: ${scheduleIndex}`);
            localStorage.setItem('schedule', scheduleIndex);
            setSchedule(scheduleIndex);
            setCurrentStepIndex(0);
            setIsRunning(false);
        }

    }
    const removeDuplicates = (array) => [...new Set(array)];

    const addSchedule = () => {

        //console.log(`addSchedule => : ${JSON.stringify(scheduleNames, null, 2)}`);
        const newSchedules = [...schedules];
        const newScheduleNames = newSchedules.map((schedule) => schedule.title);
        const scheduleIndex = newScheduleNames.length;    
        const newSchedule = prompt('Enter new schedule name:');
        newSchedules.push({ title: `${newSchedule}`, steps: [] });
        newScheduleNames.push(`${newSchedule}`); 
        const cleanNames = removeDuplicates(newScheduleNames);
        localStorage.setItem('schedules', JSON.stringify(newSchedules));
        localStorage.setItem('scheduleNames', JSON.stringify(cleanNames));
        localStorage.setItem('schedule', scheduleIndex);   
        setSchedules(newSchedules);
        setSchedule(scheduleIndex);
        setScheduleNames(cleanNames);
        setCurrentStepIndex(0);
        setIsRunning(false);

    }

    useEffect(() => {

        /* if (newStep && totalSeconds > 0) {
            setSteps([...steps, { step: newStep, timer: totalSeconds }]);
            setNewStep('');
            setNewHours('');
            setNewMinutes('');
            setNewSeconds('');
        } */
    }, [newHours, newMinutes, newSeconds]);

    useEffect(() => {
        //console.log(`StepTimer => currentTimer: ${currentTimer}`)
        if (currentTimer === 0 && isRunning) {
            Sounds['boop'](5);
        }
        if (isRunning && currentTimer > 0) {
            timerRef.current = setTimeout(() => {
                setCurrentTimer((prev) => prev - 1);
            }, 1000);
        } else if (isRunning && currentTimer === 0) {
            clearTimeout(timerRef.current);
            handleNextStep();
        }
        return () => clearTimeout(timerRef.current);
    }, [isRunning, currentTimer]);

    useEffect(() => {
        const savedState = timerState;
        const currentStep = determineStepAndTimer();
        //console.log(`timerState: ${JSON.stringify(timerState, null, 2)}`);
        //console.log(`determineStepAndTimer(): ${JSON.stringify(currentStep, null, 2)}`);
        //console.log(`StepTimer => Step Timer: ${formatTime(currentStep.currentStep?.timer)}`);
        //console.log(`StepTimer => Current Timer: ${formatTime(currentStep.currentTimer)}`);
        //console.log(`StepTimer => initStepSchedules: ${JSON.stringify(initStepSchedules, null, 2)}`);
        
        setSteps(initializeData('steps', initStepSchedules[0].steps))
        setSchedules(initializeData('schedules', initStepSchedules));
        setSchedule(initializeData('schedule', 0));
        const newSchedules = ['Add Schedule'];
        const newScheduleNames = newSchedules.map((schedule) => schedule.title);
        setScheduleNames(initializeData('scheduleNames', newScheduleNames));

        setTimeLeft(currentStep.currentTimer);
        //setSteps(initializeData('steps', []));
        setCurrentTimer(currentStep.currentTimer);
        setCurrentStepIndex(currentStep.currentStepIndex);
        updateSteps(currentStep.currentStepIndex);

        /* 
        if (savedState) {
            const {
                lastTimestamp,
                timeLeft: savedTimeLeft,
                isRunning: wasRunning,
                currentTimer: savedCurrentTimer,
                currentStepIndex: savedCurrentStepIndex
            } = savedState;

            const elapsed = Math.floor((Date.now() - lastTimestamp) / 1000);
            
            if (wasRunning) {
                console.log(`StepTimer => [0]`);
                const updatedTimeLeft = Math.max(timerState.currentTimer - elapsed, 0);

                if (elapsed > timerState.currentTimer) {

                    console.log(`StepTimer => [1]`);
                    
                    let newElapsed = elapsed - timerState.currentTimer;
                    let index = steps.slice(currentStepIndex).length;
                    let time = steps[currentStepIndex+1]?.timer;
                    
                    for (let i = currentStepIndex; i < steps.length; i++) {

                        console.log(`StepTimer => [2]${i}`);

                        if (time > newElapsed) {
                            
                            setCurrentStepIndex(i);
                            //setCurrentTimer(time - newElapsed);
                            newElapsed = 0;
                            break
                        }
                        time = time + steps[i+1].timer;
                    };

                }
                console.log(`StepTimer => [3]`);

                setTimeLeft(updatedTimeLeft);
                setCurrentTimer(Math.max(savedCurrentTimer - elapsed, 0));
                setCurrentStepIndex(savedCurrentStepIndex);

                if (updatedTimeLeft > 0) {
                    console.log(`StepTimer => [4]`);
                    startTimer();
                } else {
                    console.log(`StepTimer => [5]`);
                    moveToNextStep();
                }
            } else {
                console.log(`StepTimer => [6]`);
                setTimeLeft(timerState.currentTimer);
                setCurrentTimer(savedCurrentTimer);
                setCurrentStepIndex(savedCurrentStepIndex);
            }
            setIsRunning(wasRunning);
        } else if (steps[currentStepIndex]) {
            console.log(`StepTimer => []`);
            setTimeLeft(steps[currentStepIndex].timer);
            setCurrentTimer(steps[currentStepIndex].timer);
        } 
        */
    }, []);
    useEffect(() => {
        //console.log(`StepTimer => steps: ${JSON.stringify(steps, null, 2)}`);
        if (steps === undefined) {
            setSteps(initializeData('steps', initStepSchedules[0].steps));
        } else {
            localStorage.setItem('steps', JSON.stringify(steps));
        }

    }, [steps]);
    useEffect(() => {
        let cleanNames = [];
        if (scheduleNames === undefined || scheduleNames.length === 0) {
            const newSchedules = initializeData('scheduleNames', ['Add Schedule'])
            cleanNames = removeDuplicates(newSchedules);
            //setScheduleNames(cleanNames);
        } else {
            cleanNames = removeDuplicates(scheduleNames);
        }
        localStorage.setItem('scheduleNames', JSON.stringify(cleanNames));
    }, [scheduleNames]);
    useEffect(() => {
        if (isRunning && currentStepIndex !== undefined) {
            console.log(`StepContext => steps: ${JSON.stringify(updatedSteps(currentStepIndex), null, 2)}`);
            updateSteps(currentStepIndex);
        }
    }, [isRunning]);
    useEffect(() => {
    
        if (schedules && schedules.length > 0 && schedules[schedule] && schedules[schedule].steps) {
            if (schedules[schedule].steps.length === 0) {
                console.log(`StepContext => schedule: ${schedule} schedules[${schedule}].steps.length: ${schedules[schedule].steps.length} setStepCollapse(false)`);
                setStepCollapse(false);
            } else {
                console.log(`StepContext => schedule: ${schedule} schedules[${schedule}].steps.length: ${schedules[schedule].steps.length} setStepCollapse(true)`);
                setStepCollapse(true);
            }
            //console.log(`StepContext => schedules: ${schedules.length} schedule: ${schedule}`);
            //console.log(`StepContext => schedules[${schedule}]: ${JSON.stringify(schedules[schedule], null, 2)}`);
            //console.log(`StepTimer => schedules: ${JSON.stringify(schedules, null, 2)}`);
            console.log(`StepTimer => setSteps: ${JSON.stringify(updatedSteps(currentStepIndex), null, 2)}`);
            setSteps(updateSteps(currentStepIndex));
            //setSteps(schedules[schedule].steps)
        }
    }, [schedule]);
    useEffect(() => {
        console.log(`StepTimer => updatedStep: ${JSON.stringify(updatedSteps(currentStepIndex),null,2)} schedules: ${JSON.stringify(schedules, null, 2)}`);
        if (schedules && schedules.length > 0 && schedules[0] === null) {
            console.log(`StepTimer => schedules: ${JSON.stringify(schedules, null, 2)}`);
            const newSchedules = [ ...schedules ];
            const newScheduleNames = newSchedules.map((schedule) => schedule.title);
            const cleanNames = removeDuplicates(newScheduleNames);
            console.log(`StepTimer => newScheduleNames: ${JSON.stringify(cleanNames, null, 2)}`);
            if (cleanNames[0] !== undefined) {
                setScheduleNames(cleanNames);
            }
        }
        localStorage.setItem('schedules', JSON.stringify(schedules));
    }, [schedules]);
    useEffect(() => {
        // Persist timer state to localStorage
        //console.log(`isRunning: ${isRunning}`);
        //console.log(`StepTimer => timeLeft: ${timeLeft}`);
        //console.log(`StepTimer => currentStepIndex: ${currentStepIndex}`)
        //console.log(`StepTimer => currentTimer: ${currentTimer}`)
        if (currentStepIndex !== undefined && currentTimer !== undefined && steps !== undefined && steps.length > 0 && currentStepIndex !== undefined) {
            localStorage.setItem('timerState', JSON.stringify({
                timeLeft,
                isRunning,
                lastTimestamp: Date.now(),
                currentTimer,
                currentStepIndex
            })
            );
            localStorage.setItem('steps', JSON.stringify(steps));
            localStorage.setItem('currentStepIndex', currentStepIndex);
        }
        
    }, [timeLeft, isRunning, steps, currentStepIndex, currentTimer]);

    return (

        <StepContext.Provider value={{
            timerState,
            steps,
            setSteps,
            currentStepIndex,
            setCurrentStepIndex,
            schedules,
            setSchedules,
            scheduleNames,
            schedule,
            stepCollapse,
            setStepCollapse,
            setSchedule,
            selectSchedule,
            addSchedule,
            timeLeft,
            setTimeLeft,
            currentTimer,
            setCurrentTimer,
            isRunning,
            setIsRunning,
            formatTime,
            formatTimestamp,
            getHours,
            getMinutes,
            getSeconds,
            handleNextStep,
            handleRestartStep,
            toggleTimer,
            newStep,
            setNewStep,
            newHours, 
            setNewHours,
            editIndex, 
            setEditIndex,
            newMinutes, 
            setNewMinutes,
            newSeconds, 
            setNewSeconds,
            dialog, 
            setDialog,
            newTimer, 
            setNewTimer,
            targetElementRef
        }}>
            {
                (validate(steps) !== null)
                    ? children
                    : <div>
                        WHOOOPSIE!
                    </div>
            }
        </StepContext.Provider>
    );
};
export const useStep = () => useContext(StepContext);

export default StepsParent;